<template>
    <div class="wrap">
        <div class="bus-out">
            <div class="bus-info">
                <div class="bus-info-name">{{productList.seller.name}}</div>
                <div class="bus-info-number">
                    <div class="star">
                        <img class="star-img2" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/star.png" alt="" v-for="i in parseInt(productList.seller.score)">
                        <img class="star-img3" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/star-2.png" alt="" v-for="i in (5-parseInt(productList.seller.score))">
                    </div>
                    <span class="number-txt">({{productList.seller.ratingCount}})</span>
                    <span>月售{{productList.seller.sellCount}}单</span>
                    <div class="number-right">
                        <img v-show="!collect" @click="changeState"
                             src="http://lgting.oss-cn-beijing.aliyuncs.com/wx-img/collect.png" alt="">
                        <img v-show="collect" @click="changeState"
                             src="http://lgting.oss-cn-beijing.aliyuncs.com/wx-img/collented.png" alt="">
                        <span v-show="!collect">收藏</span>
                        <span v-show="collect">已收藏</span>
                    </div>
                </div>
            </div>
            <div class="bus-info">
                <div class="bus-detail-item" v-for="itemInfo in productList.seller.detailInfo">
                    <div class="detail-item-price">{{itemInfo.content}}</div>
                    <span class="detail-item-money">{{itemInfo.moneyBus}}</span>
                    <span class="detail-item-yuan">元</span>
                </div>
            </div>
        </div>
        <div class="bus-out">

            <div class="bus-info">
                <div class="txt-same">公告与活动</div>
                <span class="bus-info-txt">{{productList.seller.bulletin}}</span>

            </div>
            <div class="bus-info-out">
                <div class="bus-info" v-for="item in productList.seller.supports">
                    {{item.description}}
                </div>

            </div>
        </div>
        <div class="bus-out">
            <div class=" txt-same1">商家实景</div>
            <div class="bus-info-out">
                <div class="bus-img" v-for="item in productList.seller.pics">
                    <img
                            :src="item"
                            alt="">
                </div>
            </div>
        </div>

        <div class="bus-out">
            <div class="bus-info txt-same2">商家信息</div>
            <div class="bus-info-out">
                <div class="bus-info" v-for="item in productList.seller.infos">
                    <span class="bus-info-support">{{item}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import '../public/style.css'//引入公共样式
    export default {
        name: "Businessman",
        data() {
            return {
                collect: false
            }
        },
        methods: {
            changeState() {
                this.collect = !this.collect
            }
        },
        computed: {
            productList() {
                return this.$store.state.productList
            }
        }
    }
</script>

<style scoped>

    .wrap {
        position: fixed;
        top:427px;
        bottom: -15px;
        background: #EAEBED;
        overflow: scroll;
    }

    .wrap::-webkit-scrollbar {
        display: none;
    }

    .bus-info-name {
        font-size: 15px;
        font-weight: 500;
    }

    .bus-info-number {
        font-size: 12px;
    }

    .number-txt {
        margin-right: 5px;
        margin-top: 5px;
        vertical-align: top;
    }

    .number-right {
        display: inline-block;
        width: 40px;
        text-align: center;
        height: 53px;
        float: right;
        margin-right: 20px;
        margin-top: -24px;
    }

    .number-right span {
        width: 100%;
    }

    .number-right img {
        width: 25px;
        height: 25px;
    }

    .detail-item-price {
        width: 100%;
    }

    .bus-detail-item {
        width: 33%;
        display: inline-block;
        text-align: center;
        border-right: 1px solid #EAEBED;;
    }

    .detail-item-price {
        font-size: 13px;

    }

    .detail-item-money {
        font-size: 23px;
        color: black;
    }

    .detail-item-yuan {
        font-size: 13px;
        margin-left: -3px;
    }

    .bus-info-txt {
        display: inline-block;
        width: 100%;
        color: red;
        width: 95%;
        font-size: 12px;
        line-height: 21px;
        overflow: hidden;
        padding: 0px 10px 0px 10px;
    }

    .bus-info {
        font-size: 13px;

    }

    .txt-same {
        font-size: 20px;
    }

    .txt-same1 {
        padding: 12px;
        font-size: 16px;

    }

    .bus-img {
        display: inline-block;

        padding-left: 13px;
        padding-bottom: 10px;
    }

    .bus-img img {
        width: 120px;
        height: 89px;
    }

    .txt-same2 {
        font-size: 16px;
    }

    .bus-info-support {
        padding-left: 13px;
        color: black;
    }
.star{
    display: inline-block;
    margin-top: 11px;
    vertical-align: middle;
}
    .star-img2{
        width: 20px;
        height: 20px;
        vertical-align: top;
        margin-right: 4px;
        margin-top: -9px;
        margin-left: -3px;
    }
    .star-img3{
        width: 20px;
        height: 20px;
        vertical-align: top;
        margin-top: -9px;
        margin-left: -7px;
        margin-right: 6px;
    }
</style>


